<template>
  <div
    class="home"
    :class="{ min: screenWidth < 700, med: screenWidth < 1200 }"
  >
    <!-- 页头部分 -->
    <div class="header">
      <p class="title">
        <span class="name">次顶级</span>
        <span class="describe">了解真相，才能获得真正的自由！</span>
      </p>
    </div>
    <!-- 底部内容部分，包括导航和内容 -->
    <div class="contentBox">
      <!-- 导航盒子 -->
      <div class="navBox">
        <!-- 导航盒子布局 -->
        <div class="navBox_border" :class="{ clearPadding: !switchNavBox }">
          <!-- 导航组件 -->
          <my-transition>
            <MyNav
              v-show="switchNavBox"
              :actNav="actNav"
              :navList="navList"
              @changeNavAct="changeNavAct"
            ></MyNav>
          </my-transition>
          <!-- 切换整个导航显示隐藏按键 -->
          <div class="navBut" @click="switchNavBox = !switchNavBox">
            {{ switchNavBox ? "收起导航" : "展开导航" }}
          </div>
        </div>
      </div>
      <!-- 内容部分 -->
      <div class="content">
        <router-view />
      </div>
    </div>
  </div>
</template>
<script>
// 自定义过度组件
import myTransition from "../utils/donghua.js";
// 导航组件
import MyNav from "../components/myNav.vue";
export default {
  name: "Home",
  components: {
    MyNav,
    "my-transition": myTransition,
  },
  data() {
    return {
      screenWidth: "", //页面宽度
      switchNavBox: true, //收起展开控制
      actNav: "学习笔记", //当前选中的导航
      navList: [
        //多层级导航数据
        {
          name: "学习笔记",
          index: "notes",
        },
        {
          name: "个人简介",
          index: "about",
        },
      ],
    };
  },
  created() {},
  mounted() {
    this.monitorWidth(); //监听页面宽度
  },

  methods: {
    // 监听页面宽度
    monitorWidth() {
      //初始化页面宽度
      this.screenWidth = document.body.clientWidth;
      window.addEventListener("resize", () => {
        //页面宽度变化继续监听
        this.screenWidth = document.body.clientWidth;
      });
    },
    // 导航变化
    changeNavAct(name, index) {
      this.actNav = name; //当前选中页面
      this.$router.push({ path: index }); //跳转对应页面
    },
  },
};
</script>
