import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    redirect: '/notes',
    children:[
      {
        path:'/notes',
        name:'notes',
        component:()=>import ('../pages/notes.vue'),   //文章列表
      },
      {
        path:'/details',
        name:'details',
        component:()=>import ('../pages/details.vue'),    //文章详情
      },
      {
        path:'/about',
        name:'about',
        component:()=>import ('../pages/about.vue'),     //个人介绍
      },
      
      
    
    ]
  },
  
  
]
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
